import React,{ useState } from 'react';
import './App.css';
import Weather from './Components/Weather';

function App() {
  const api_key = '9337a55ff893c9f7dbbaa956a24f80d1';

  const [city, setCity] = useState('');
  const [Temp, setTemp] = useState('');
  const [Description, setDescription] = useState('');

  const calTemp = (temp) => {
    return Math.floor(temp - 273.15);
  };

  

  // api call
  const getWeather = async (e) => {
    e.preventDefault();

    const value = e.target.elements.place.value;
    const place = value[0].toUpperCase() +  value.slice(1); 

    const apiCall = await fetch(
      'https://api.openweathermap.org/data/2.5/weather?q=' +
        place +
        '&appid=' +
        api_key
    );
    const response = await apiCall.json();
    if(place === response.name){
      setCity(response.name);
      setTemp(calTemp(response.main.temp));
      setDescription(response.weather[0].description);
    }
    else{
      alert("Please enter a city name!");
    }
    
   
  };
  return (
    <div className="App">
      <div className="container">
        <form onSubmit={getWeather}>
          <input type="text" placeholder="Enter City Name" name="place"/>
          <input type="submit" value="Get Weather"/>
        </form>
        {city?<Weather 
          city={city}
          temp={Temp}
          description={Description}
        />:""}
      </div>
    </div>
  );
}

export default App;
